.progress {
    position: fixed;
    left: 0;
    right: 0;
    height: 5px;
    background: #0345ca;
    top: 5px;
}

body {
    background: rgb(15, 23, 42);
}

.progress-container {
    position: fixed;
}

.home-header {
    padding: 5px 30px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 2400px;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    margin: auto;
    background-color: transparent;
    z-index: 200 !important;
}

.logo {
    font-size: 2rem;
    width: 200px;
    margin-top: 10px;
}

.bar {
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 2400px;
    padding: 0px;
    margin: 0px auto;
}

.section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12.5px;
    width: 100%;
}

.links {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12.5px;
}

.link-buy {
    cursor: pointer;
    transition: all 0.25s ease 0s;
    font-size: 0.975rem;
    font-weight: 400;
    color: rgb(255, 255, 255);
    text-decoration: none;
    border-radius: 15px;
    background-color: #0345ca;
    height: 37.5px;
    padding: 0px 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.css-vubbuv {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 1em;
    height: 1em;
    display: inline-block;
    fill: currentColor;
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    -webkit-transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-size: 1.5rem;
}

.first-section {
    display: flex;
    position: relative;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    width: 100%;
    height: 100vh;
    padding: 0px 150px;
    color: rgb(255, 255, 255);
    opacity: 1;
    transform: none;
}

.section-header {
    font-size: 2.5rem;
    font-weight: 900;
    padding: 0px !important;
    margin: 0px !important;
}

.section-info {
    font-size: 1.4rem;
}

.section1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 0px;
    max-width: 600px;
    z-index: 1;
}

.section2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 10px;
    max-width: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cloud-svg {
    color: rgb(255, 255, 255);
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cloudSvg {
    width: 275px;
}

.link-buttons {
    display: flex;
    gap: 10px;
}

.second-section {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    width: 100%;
    height: 100vh;
    padding: 0px 150px;
    color: rgb(255, 255, 255);
    position: relative !important;
}

.icons {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
}

@media only screen and (max-width: 1700px) {
    .second-section {
        justify-content: center;
    }
}

.css-vubbuv {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 1em;
    height: 1em;
    display: inline-block;
    fill: currentColor;
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    -webkit-transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-size: 1.5rem;
}

.icons .mui-icon {
    color: white;
    font-size: 3rem !important;
    width: 3rem !important;
    margin-right: 7.5px;
}